import { useContext, useState } from 'react';

import Modal from '../UI/Modal';
import CartItem from './CartItem';
import classes from './Cart.module.css';
import CartContext from '../../store/cart-context';
import CheckOutForm from './CheckOutForm';
import useHttp from '../../hooks/use-http';

const Cart = (props) => {
  const cartCtx = useContext(CartContext);

  const totalAmount = `$${cartCtx.totalAmount.toFixed(2)}`;
  const hasItems = cartCtx.items.length > 0;

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');

  const cartItemRemoveHandler = (id) => {
    cartCtx.removeItem(id);
  };

  const cartItemAddHandler = (item) => {
    cartCtx.addItem(item);
  };

  const nameChangeHandler = (value) =>{
    setName(value)
  }

  const emailChangeHandler =(value) =>{
    setEmail(value);
  }


  const {fetchData} = useHttp('orders',{method:'POST'})

  const orderPlacedHandler = () => {
    fetchData({
      name:name,
      email:email,
      cart:cartCtx.items
    })
  }

  const cartItems = (
    <ul className={classes['cart-items']}>
      {cartCtx.items.map((item) => (
        <CartItem
          key={item.id}
          name={item.name}
          amount={item.amount}
          price={item.price}
          onRemove={cartItemRemoveHandler.bind(null, item.id)}
          onAdd={cartItemAddHandler.bind(null, item)}
        />
      ))}
    </ul>
  );

  return (
    <Modal onClose={props.onClose}>
      {cartItems}
      <div className={classes.total}>
        <span>Total Amount</span>
        <span>{totalAmount}</span>
      </div>
      <div>
        <CheckOutForm onNameChange={nameChangeHandler} onEmailChange={emailChangeHandler}/>
      </div>
      <div className={classes.actions}>
        <button className={classes['button--alt']} onClick={props.onClose}>
          Close
        </button>
        {hasItems && <button className={classes.button} onClick={orderPlacedHandler}>Order</button>}
      </div>
    </Modal>
  );
};

export default Cart;
